import { createMarkdownPlatePlugin } from 'topwrite';
import { ReactComponent as Icon } from './images/robot.svg';
import { useState } from 'react';
import Copilot from './components/copilot';

export const AiPlugin = createMarkdownPlatePlugin({
    hoverTools() {
        return {
            5: function ({ Component }) {

                const [show, setShow] = useState(false);

                return <>
                    <Component
                        handler={() => setShow(!show)}
                        active={show}
                        icon={<Icon />}
                    />
                    {show && <Copilot />}
                </>;
            }
        };
    }
});