import { styled } from "topwrite";
import { ReactComponent as TranslateIcon } from '../../images/translate.svg';
import { ReactComponent as ExtendIcon } from '../../images/extend.svg';
import { ReactComponent as ShortenIcon } from '../../images/shorten.svg';
import { ReactComponent as RunIcon } from '../../images/run.svg';
import { ReactComponent as PolishIcon } from '../../images/polish.svg';
import { Consumer } from "./context";
import Item from "./item";
import Result from "./result";
import useConfig from "@@/use-config";

export default function Copilot() {

    const config = useConfig();

    const language = config.getValue(['translate', 'target'], '英文');

    return <Container>
        <Consumer>
            {({ loading, result }) => {
                if (loading || result) {
                    return <Result />;
                }
                return <Menu className="list-group">
                    <Item
                        icon={<PolishIcon />}
                        title="润色"
                        prompt="我希望你能帮我润色下面的内容，使其更加流畅，优美，尽量保留原有的Markdown格式。请润色以下内容：\n\n{content}"
                    />
                    <Item
                        icon={<ExtendIcon />}
                        title="续写"
                        prompt="下面我让你来充当文档{book}作者，{chapter}我希望你能帮我续写下面的内容。请续写以下内容：\n\n{content}"
                        append
                    />
                    <Item
                        icon={<ShortenIcon />}
                        title="精简"
                        prompt="我希望你能帮我将下面的内容精简一下，尽量保留原有的Markdown格式。请精简以下内容：\n\n{content}"
                    />
                    <Item
                        icon={<TranslateIcon />}
                        title={`翻译为${language}`}
                        prompt={`请将以下内容翻译成${language}：\n\n{content}`}
                    />
                    <Item
                        icon={<RunIcon />}
                        title="执行"
                        prompt="{content}"
                    />
                </Menu>;
            }}
        </Consumer>
    </Container>;
}

const Menu = styled.div`
    width: 150px;
    --bs-list-group-bg: var(--ttw-box-background);
    --bs-list-group-color: var(--ttw-box-color);
    --bs-list-group-action-hover-color: var(--ttw-box-color);
    --bs-list-group-action-hover-bg: var(--ttw-box-hover-background);
    --bs-list-group-action-active-color: var(--ttw-box-color);
    --bs-list-group-action-active-bg: var(--ttw-box-active-background);
    --bs-list-group-border-width: 0;

    .list-group-item {
        display: flex;
        align-items: center;
        gap: 10px;

        svg {
            width: 14px;
            height: 14px;
        }
    }
`;

const Container = styled.div`
    position: absolute;
    top: 38px;
    left: 0;
    line-height: 1.5;
    box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 15%);
    user-select: auto;
    background-color: var(--ttw-box-background);
    color: var(--ttw-box-color);
    border-radius: 0.375rem;
`;
