import { ReactNode, useCallback, MouseEvent, useContext } from "react";
import Context, { Message } from "./context";
import { useBook, useEditor, useFile } from "topwrite";

interface ItemProps {
    icon: ReactNode;
    title: string;
    prompt: string | ((content: string) => Message[]);
    append?: boolean;
}

export default function Item({ icon, title, prompt, append }: ItemProps) {

    const { run } = useContext(Context);

    const editor = useEditor();
    const { config, summary } = useBook();
    const file = useFile();

    const article = summary.getArticle(article => article.ref === file.path);

    const handler = useCallback(async (e: MouseEvent) => {
        e.preventDefault();

        const text = editor.serializer.serialize(editor.getFragment());

        let messages: Message[] = [];
        if (typeof prompt === 'function') {
            messages = prompt(text);
        } else {
            const title = config.getValue('title');
            const book = title ? `【${title}】的` : '';
            const chapter = article ? `正在编写章节[${article.title}]，` : '';

            const content = prompt.replace('{content}', text).replace('{book}', book).replace('{chapter}', chapter);

            messages = [{
                role: 'user',
                content
            }];
        }

        await run({ messages, append });

    }, [editor, config, article]);

    return <div
        className="list-group-item list-group-item-action"
        role='button'
        onMouseDown={handler}
    >
        {icon}{title}
    </div>;
}