import { useCallback, useContext, useEffect, useRef } from 'react';
import { styled, Button, useEditor } from 'topwrite';
import Context from './context';
import { ReactComponent as LoadingIcon } from '../../images/three-dots.svg';
import { ReactComponent as StopIcon } from '../../images/stop.svg';
import { ReactComponent as ReloadIcon } from '../../images/reload.svg';

export default function Result() {

    const { loading, result, controller, run, append, error } = useContext(Context);

    const editor = useEditor();

    const scrollRef = useRef<HTMLDivElement>(null);

    const scrollToBottom = useCallback(() => {
        const dom = scrollRef.current;
        if (dom) {
            requestAnimationFrame(() => dom.scrollTo(0, dom.scrollHeight));
        }
    }, []);

    useEffect(() => {
        scrollToBottom();
    });

    const insert = useCallback(() => {
        if (result) {
            const header = append ? editor.serializer.serialize(editor.getFragment()) : '';
            const fragment = editor.serializer.deserialize(header + result);
            editor.insertFragment(fragment);
        }
    }, [result, append]);

    const abort = useCallback(() => {
        controller?.abort();
    }, [controller]);

    const reload = useCallback(() => {
        run();
    }, []);

    return <Container>
        {!result ? <LoadingIcon /> : <>
            <Text ref={scrollRef}>{result}</Text>
            <Footer>
                <div>
                    {loading ? <Button variant='secondary' onMouseDown={abort}>
                        <StopIcon />
                    </Button> : <Button variant='secondary' onMouseDown={reload}>
                        <ReloadIcon />
                    </Button>}
                </div>
                <div>
                    <Button loading={loading} disabled={error} onMouseDown={insert}>插入</Button>
                </div>
            </Footer>
        </>}
    </Container>;
}

const Footer = styled.div`
    justify-content: space-between;
    display: flex;
`;

const Text = styled.div`
    background-color: var(--ttw-background);
    border: 1px solid var(--ttw-border-color);
    padding: .5rem;
    border-radius: 0.375rem;
    height: 250px;
    font-size: 15px;
    white-space: pre-wrap;
    word-break: break-all;
    overflow-x: hidden;
    overflow-y: auto;
`;

const Container = styled.div`
    padding: 1rem;
    width: 500px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
`;
