import { ConsumerProps, createContext, useCallback, useEffect, useState } from "react";
import { EventStreamContentType, fetchEventSource, } from '@fortaine/fetch-event-source';
import useConfig from "@@/use-config";

export interface Message {
    role: string;
    content: string;
}

interface ContextProps {
    loading: boolean;
    result: string;
    append: boolean;
    error: boolean;
    controller?: AbortController;
    run: (options?: { messages: Message[]; append?: boolean; }) => Promise<void>;
}

const Context = createContext<ContextProps>({
    loading: false,
    result: '',
    append: false,
    error: false,
    controller: undefined,
    run: () => Promise.resolve(),
});

export default Context;

export const Consumer = function ({ children }: ConsumerProps<ContextProps>) {

    const config = useConfig();
    const [loading, setLoading] = useState(false);
    const [result, setResult] = useState('');
    const [controller, setController] = useState<AbortController>();
    const [messages, setMessages] = useState<Message[]>([]);
    const [append, setAppend] = useState(false);
    const [error, setError] = useState(false);

    const run = useCallback(async (options?: { messages: Message[]; append?: boolean; }) => {

        const endpoint = config.getValue('endpoint', 'https://ai.topthink.com/chat/completions');
        const token = config.getValue('token');
        if (!token) {
            setResult('[令牌未设置]');
            setError(true);
            return;
        }

        let msgs: Message[];

        if (options) {
            setMessages(options.messages);
            setAppend(options.append ?? false);
            msgs = options.messages;
        } else {
            msgs = messages;
        }

        setResult('');
        setLoading(true);
        setError(false);

        let finished = false;

        const controller = new AbortController();

        controller.signal.onabort = () => {
            finished = true;
            //取消
            setLoading(false);
            setController(undefined);
        };

        setController(prev => {
            prev?.abort();
            return controller;
        });

        await fetchEventSource(endpoint, {
            method: 'POST',
            body: JSON.stringify({
                messages: msgs,
            }),
            signal: controller.signal,
            headers: {
                'content-type': 'application/json',
                'accept': 'text/event-stream,application/json',
                'authorization': `Bearer ${token}`
            },
            async onopen(res) {
                if (
                    !res.ok ||
                    !res.headers.get('content-type')?.startsWith(EventStreamContentType)
                ) {
                    finished = true;

                    let errorMessage = '网络错误，请重试';

                    if (res.status === 403 || res.status === 401) {
                        errorMessage = '令牌已过期或无权限';
                    } else {
                        try {
                            const resJson = await res.clone().json();
                            errorMessage = resJson.message || '';
                        } catch {
                        }
                    }

                    setResult(`[${errorMessage}]`);
                    setError(true);
                }
            },
            onmessage(msg) {
                if (msg.data === '[DONE]' || finished) {
                    finished = true;
                    return;
                }

                try {
                    const text: string = JSON.parse(msg.data);
                    setResult(result => result + text);
                } catch {

                }
            },
            onerror(err) {
                finished = true;
                //完成
                setLoading(false);
                setController(undefined);
                setResult(`[${err.message}]`);
                setError(true);
            },
            onclose() {
                finished = true;
                //完成
                setLoading(false);
                setController(undefined);
            },
            openWhenHidden: true,
        });
    }, [messages, config]);

    useEffect(() => {
        return () => {
            controller?.abort();
        };
    }, [controller]);

    return <Context.Provider value={{ loading, result, append, error, controller, run }}>
        <Context.Consumer>{children}</Context.Consumer>
    </Context.Provider>;
};
